.page {
    // visibility: hidden;
    // opacity: 0;
    &.page-enter {
      opacity: 0;
    }
  
    &.page-enter-active {
      opacity: 1;
      transition: opacity 300ms ease-in-out;
    }
  
    &.page-enter-done {
      opacity: 1;
    }
  
    &.page-exit {
      opacity: 1;
    }
  
    &.page-exit-active {
      opacity: 0;
      transition: opacity 300ms ease-in-out;
      visibility: hidden;
    }
  }


  
  
  
  